import React, { Component } from 'react';
import starwarsService from '../services/starwars.service';

export default class DetailView extends Component {
  static getInitialProps ({ query: { id } }) {
    return { personID: id };
  }

  constructor(props) {
    super(props);

    this.state = {
      data: {}
    };
  }

  componentWillMount() {
    starwarsService.getPersonById(this.props.personID)
      .then(res => {
        this.setState({
          data: res.data.getPerson
        });
      })
      .catch(console.error);
  }

  render () {
    return (
      <section className="detail-view">
        <style global jsx>{`
          body,
          html {
            background-color: #151515;
            margin: 0;
            padding: 0;
            color: #f4f4f4;
            font-family: 'Raleway', cursive;
          }
          
          .list-view__logo {
            max-width: 100%;
          }

          .list-view__logo-container {
            max-width: 30%;
            margin: 32px auto;
          }
          
          .ui-detail {
            background-color: #f5f5f5;
            max-width: 50%;
            margin: 0 auto;
            border-radius: 4px;
            color: #333;
            padding: 16px;
            box-sizing: border-box;
          }

          .ui-detail__title {
            margin: 0;
            padding: 0;
          }
        `}</style>
        <div className="list-view__logo-container">
          <img className="list-view__logo" src="https://www.clipartmax.com/png/full/6-65467_star-wars-logo-png-file-star-wars-transparent-background.png" />
        </div>

        <article className="ui-detail">
          <div className="ui-detail__image">
            <img />
          </div>
          <div className="ui-detail__info">
            <h1 className="ui-detail__title">{this.state.data.name}</h1>
          </div>
        </article>
      </section>
    );
  }
}
